import React from "react";
import { Stack, Box } from "@mui/material";
import { VideoCard, ChannelCard } from "./";

const Videos = ({ videos, direction }) => {

  console.log('Videos en el componente Videos:', videos);

  const videoCardArray = Array.from({ length: 50 }, (_, index) => {
    
    const fakeVideoData = {
      id: `fake_video_${index}`,
      title: `Titulo de Ejemplo ${index}`,
      duration: 200 + (index * 20),
      author: `Autor ${index}`,
      
    };

    return (
      <Box key={fakeVideoData.id} mb={2}>
        {/* Pasa los datos del video ficticio al componente VideoCard */}
        <VideoCard video={fakeVideoData} />
      </Box>
    );
  });

  return (
    <Stack direction={direction || "row"} flexWrap="wrap" justifyContent="start" gap={2}>
      {/* Muestra un solo ChannelCard al principio */}
      <Box mb={2}>
        <ChannelCard />
      </Box>

      {videoCardArray}
    </Stack>
  );
}

export default Videos;
