import React, { useEffect } from 'react'
import { useState } from 'react';
import { Box, Stack, Typography } from '@mui/material';
import { SideBar, Videos } from './';
import { fetchData } from '../utils/api.js';
import Images from './Images.jsx'

const Feed = () => {

  const [selectedCategory, setSelectedCategory] = useState('Nuevo');
  const [videos, setVideos] = useState([]);
  const [videoCount, setVideoCount] = useState(0);
  const [mode, setMode] = useState('videos'); // Add a mode state

  useEffect(() => {
    // Utiliza la función fetchData desde el archivo api.js
    fetchData(selectedCategory, setVideoCount, setVideos);
    console.log('El parametro Videos Contiene', videos);
  }, [selectedCategory]);

  return (
    <Stack sx={{ flexDirection: { sx: "column", md: "row" } }}>
      <Box sx={{ height: { sx: 'auto', md: '92vh' }, borderRight: '1px solid #3d3d3d', px: { sx: 0, md: 2 } }}>
        <SideBar
          selectedCategory=
          {selectedCategory}
          setSelectedCategory=
          {setSelectedCategory}
        />

        <Typography className='copyright'
          variant='body2' sx={{ mt: 1.5, color: "#fff" }}>
          Copyright 2023 Antonio Guanilo
        </Typography>
      </Box>

      <Box p={2} sx={{ overflowY: 'auto', height: '90vh', flex: 2 }}>
        <Typography variant='h4' fontWeight="bold" mb={2} sx={{ color: 'white' }}>
          {selectedCategory} <span style={{ color: '#F31503' }}>{mode === 'videos' ? 'videos' : 'images'}</span>
        </Typography>

        {mode === 'videos' ? (
          <Videos videos={videos} />
        ) : (
          <Images/>
        )}

      </Box>
    </Stack>
  )
}

export default Feed