// VideoFormModal.jsx
import React, { useState, useEffect } from 'react';
import { Modal, Button, FormControl, InputLabel, Select, MenuItem, Stack, Box } from '@mui/material';
import Snackbar from '@mui/material/Snackbar';
import MuiAlert from '@mui/material/Alert';

const VideoFormModal = ({ isOpen, handleClose, videos, categories }) => {

    const [selectedVideo, setSelectedVideo] = useState('');
    const [selectedCategories, setSelectedCategories] = useState([]);
    const [selectedVideoThumbnail, setSelectedVideoThumbnail] = useState('');
    const [snackbarOpen, setSnackbarOpen] = useState(false);
    const [snackbarMessage, setSnackbarMessage] = useState('');
    const [snackbarType, setSnackbarType] = useState('success');

    const handleSnackbarClose = () => {
        setSnackbarOpen(false);
        setSnackbarMessage('');
        setSnackbarType('success');
    };


    useEffect(() => {
        // Actualiza la vista previa del thumbnail cuando se selecciona un video
        const selectedVideoData = videos.find((video) => video.id === selectedVideo);
        if (selectedVideoData) {
            setSelectedVideoThumbnail(selectedVideoData.thumbnail_path);
        }
    }, [selectedVideo, videos, isOpen]);

    const handleAssignCategories = () => {
        // Intenta realizar la solicitud al puerto 5000
        fetchAssignCategories("http://localhost:5000/assign_categories", selectedVideo, selectedCategories);
    };
    
    const fetchAssignCategories = async (url, videoId, categoryIds) => {
        try {
            const response = await fetch(url, {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                },
                body: JSON.stringify({
                    video_id: videoId,
                    category_ids: categoryIds,
                }),
            });
    
            const data = await response.json();
            console.log(data.message);
            setSnackbarType("success");
            setSnackbarOpen(true);
        } catch (error) {
            // Si hay un error al acceder al puerto 5000, intentar con el puerto 8000
            if (url.includes("5000")) {
                fetchAssignCategories("http://localhost:8000/assign_categories", videoId, categoryIds);
            } else {
                // Manejar el caso en que ambos puertos fallan
                setSnackbarMessage("Hubo un problema al asignar categorías.");
                setSnackbarType("error");
                setSnackbarOpen(true);
                handleClose();
            }
        } finally {
            handleClose(); // Cierra el modal después de la asignación o en caso de error.
        }
    };
    
    

    return (
        <Modal open={isOpen} onClose={handleClose}>
            <Box sx={{
                width: 400,
                bgcolor: 'black', // Color de fondo negro
                p: 2,
                position: 'absolute',
                top: '50%',
                left: '50%',
                transform: 'translate(-50%, -50%)',
                color: 'white', // Color del texto blanco
                borderRadius: 2, // Bordes redondeados
                border: '2px solid #FBC101', // Borde rojo
            }}>
                <img
                    src={selectedVideoThumbnail}
                    alt="Thumbnail"
                    style={{ width: '100%', maxHeight: '300px', objectFit: 'cover' }}
                />
                <FormControl fullWidth variant="outlined" sx={{
                    '& .MuiOutlinedInput-notchedOutline': { borderColor: 'white' },
                    '& label': { color: 'white' },
                    mt: 1,
                    mb: 2
                }}>
                    <InputLabel>{selectedVideo ? videos.find(video => video.id === selectedVideo)?.title || 'Selecciona un video' : 'Selecciona un video'}</InputLabel>
                    <Select
                        value={selectedVideo}
                        onChange={(e) => setSelectedVideo(e.target.value)}
                        MenuProps={{
                            MenuListProps: {
                                style: {
                                    maxHeight: 300,
                                    overflowY: 'auto',
                                },
                            },
                        }}
                    >
                        {videos.map((video, index) => (
                            <MenuItem key={video.id} value={video.id}>
                                {`${index + 1}. ${video.title}`}
                            </MenuItem>
                        ))}
                    </Select>
                </FormControl>
                <FormControl fullWidth variant="outlined" sx={{
                    '& .MuiOutlinedInput-notchedOutline': { borderColor: 'white' },
                    '& label': { color: 'white' },
                    mt: 1,
                    mb: 2
                }}>
                    <InputLabel>Selecciona categorías</InputLabel>
                    <Select
                        multiple
                        value={selectedCategories}
                        onChange={(e) => setSelectedCategories(e.target.value)}
                        MenuProps={{
                            MenuListProps: {
                                style: {
                                    maxHeight: 300,
                                    overflowY: 'auto',
                                },
                            },
                        }}
                    >
                        {categories.map((category) => (
                            <MenuItem
                                key={category.id}
                                value={category.id}
                                sx={{
                                    backgroundColor: selectedCategories.includes(category.id) ? 'yellow' : 'inherit',
                                    '&:hover': {
                                        backgroundColor: '#FEFB8F',
                                    },
                                    '&.Mui-selected': {
                                        backgroundColor: '#FED402',
                                    },
                                }}
                            >
                                {category.name}
                            </MenuItem>
                        ))}
                    </Select>
                </FormControl>
                <Button
                    variant="contained"
                    onClick={handleAssignCategories}
                    mt={2}
                    sx={{ backgroundColor: '#FBC101', '&:hover': { backgroundColor: '#FBC101' } }}
                >
                    Asignar Categorías
                </Button>
            </Box>
        </Modal>
    );
};

export default VideoFormModal;
