import React from 'react';
import { Link } from 'react-router-dom';
import { Typography, Card, CardContent, CardMedia, Box } from '@mui/material';
import { CheckCircle } from '@mui/icons-material';
import thumnbnailImage from '../assets/images/img.jpg';

import { demoVideoUrl, demoVideoTitle, demoChannelUrl, demoChannelTitle } from '../utils/constants';

const formatDuration = (seconds) => {
  const hours = Math.floor(seconds / 3600);
  const minutes = Math.floor((seconds % 3600) / 60);
  const remainingSeconds = seconds % 60;

  // Formato con ceros a la izquierda
  const pad = (value) => (value < 10 ? `0${value}` : value);

  return `${pad(hours)}:${pad(minutes)}:${pad(remainingSeconds)}`;
};

const VideoCard = ({ video }) => {
  // Verificar si 'video' está definido
  if (!video) {
    return <div>Error: Video no proporcionado</div>;
  }

  console.log('Datos del video en VideoCard:', video);

  // Lógica condicional antes de los Hooks
  const thumbnailPath = video.thumbnail_path;
  const title = video.title;
  const author = video.author;
  const durationInSeconds = video.duration;
  const formattedDuration = formatDuration(durationInSeconds);
  const videoUrl = `/video/${video.id}`;

  // Resto del componente
  return (
    <Card sx={{ width: { xs: '100%', sm: '358px', md: '320px' }, boxShadow: 'none', borderRadius: 0 }}>
      <Link to={videoUrl}>
        <CardMedia
          image={thumnbnailImage}
          alt="title"
          sx={{ width: { xs: '100%', sm: '358px' }, height: 180, position: 'relative' }}
        >
          <Box
            sx={{
              position: 'absolute',
              bottom: '8px',
              right: '40px',
              backgroundColor: 'rgba(0, 0, 0, 0.5)',
              padding: '4px 8px',
              borderRadius: '4px',
              maxWidth: '50px', // Ajusta según sea necesario
            }}
          >
            <Typography variant="body2" color="white">
              {formattedDuration} {/* Mostrar la duración del video */}
            </Typography>
          </Box>
        </CardMedia>
        <CardContent sx={{ backgroundColor: '#1e1e1e', height: '96px' }}>
          <Link to={videoUrl}>
            <Typography variant="subtitle1" fontWeight="bold" color="#FFF" title={title}>
              {title.slice(0, 60)}
            </Typography>
          </Link>
          <Link to={videoUrl}>
            <Typography variant="subtitle2" fontWeight="bold" color="gray">
              {author}
              <CheckCircle sx={{ fontSize: '12px', color: 'gray', ml: '5px' }} />
            </Typography>
          </Link>
        </CardContent>
      </Link>
    </Card>
  );
};

export default VideoCard;
