export const fetchData = async (selectedCategory, setVideoCount, setVideos) => {
  try {
    // Intentar obtener el número de videos por categoría desde el puerto 5000
    const countResponse = await fetch(`http://localhost:5000/get_video_count_by_category/${selectedCategory}`);
    const countData = await countResponse.json();
    setVideoCount(countData.video_count);
    console.log(`Número de videos en ${selectedCategory}:`, countData.video_count);

    // Intentar obtener los datos de los videos desde el puerto 5000
    const videosResponse = await fetch(`http://localhost:5000/get_videos/${selectedCategory}`);
    const videosData = await videosResponse.json();
    setVideos(videosData);
    console.log('Videos obtenidos:', videosData);
  } catch (error) {
    // Si hay un error al acceder al puerto 5000, intentar con el puerto 8000
    try {
      // Obtener el número de videos por categoría desde el puerto 8000
      const countResponse = await fetch(`http://localhost:8000/get_video_count_by_category/${selectedCategory}`);
      const countData = await countResponse.json();
      setVideoCount(countData.video_count);
      console.log(`Número de videos en ${selectedCategory} (puerto 8000):`, countData.video_count);

      // Obtener los datos de los videos desde el puerto 8000
      const videosResponse = await fetch(`http://localhost:8000/get_videos/${selectedCategory}`);
      const videosData = await videosResponse.json();
      setVideos(videosData);
      console.log('Videos obtenidos (puerto 8000):', videosData);
    } catch (error8000) {
      // Si hay un error al acceder al puerto 8000, imprimir el error
      console.error('Error en ambos puertos (5000 y 8000):', error8000);
    }
  }
};
