// VideoFormModal.jsx

import React, { useState, useEffect } from 'react';
import { Modal, Box, TextField, Button, Select, MenuItem, InputLabel, FormControl, Snackbar } from '@mui/material';

const VideoForModal = ({ isOpen, handleClose }) => {
    const [videoData, setVideoData] = useState({
        thumbnail_path: '',
        title: '',
        views: 0,
        duration: '00:00:00',
        likes: 0,
        description: '',
        video_path: '',
        author: '',
    });


    const [errorOpen, setErrorOpen] = useState(false);
    const [successOpen, setSuccessOpen] = useState(false);

    const handleCloseError = () => {
        setErrorOpen(false);
    };

    const handleCloseSuccess = () => {
        setSuccessOpen(false);
        handleClose(); // Cierra el modal después de mostrar el mensaje de éxito
    };

    const handleInputChange = (e) => {
        const { name, value } = e.target;

        const numericValue = isNaN(value) ? value : parseFloat(value);

        setVideoData((prevData) => ({
            ...prevData,
            [name]: numericValue,
        }));
    };

    const handleCategoryChange = (event) => {
        // Manejar cambios en las categorías seleccionadas
        const selectedCategories = event.target.value;
        setVideoData((prevData) => ({
            ...prevData,
            categories: selectedCategories,
        }));
    };

    const handleSubmit = async () => {
        // Verificar si algún campo está en blanco
        if (Object.values(videoData).some((value) => value === '' || value === 0)) {
            setErrorOpen(true);
            return;
        }

        // Intentar enviar la solicitud al puerto 5000
        try {
            await sendVideoRequest("http://localhost:5000/add_video", videoData);
            setSuccessOpen(true);
        } catch (error8000) {
            // Si hay un error al acceder al puerto 5000, intentar con el puerto 8000
            try {
                await sendVideoRequest("http://localhost:8000/add_video", videoData);
                setSuccessOpen(true);
            } catch (error5000) {
                console.error("Error adding video:", error5000);
                setErrorOpen(true);
            }
        }
    };

    const sendVideoRequest = async (url, videoData) => {
        const response = await fetch(url, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(videoData),
        });

        if (!response.ok) {
            throw new Error(`Error adding video: ${response.status}`);
        }
    };

    return (
        <Modal open={isOpen} onClose={handleClose}>
            <Box sx={{
                width: 400,
                bgcolor: 'black', // Color de fondo negro
                p: 2,
                position: 'absolute',
                top: '50%',
                left: '50%',
                transform: 'translate(-50%, -50%)',
                color: 'white', // Color del texto blanco
                borderRadius: 2, // Bordes redondeados
                border: '2px solid #ED4343', // Borde rojo
            }}>
                <h2>Agregar Video</h2>
                <TextField
                    name="thumbnail_path"
                    label="Thumbnail Path"
                    variant="outlined"
                    fullWidth
                    color="primary"
                    sx={{
                        mt: 1,
                        '& input': { color: 'white' },
                        '& .MuiOutlinedInput-notchedOutline': { borderColor: 'white' }, // Color del borde
                        '&:hover .MuiOutlinedInput-notchedOutline': { borderColor: 'white' }, // Color del borde al pasar el ratón
                        '& .Mui-focused .MuiOutlinedInput-notchedOutline': { borderColor: '#ED4343' }, // Color del borde cuando está enfocado
                        '& .MuiInputLabel-root': { color: 'white' },
                        mb: 2
                    }}
                    onChange={handleInputChange}
                />
                <TextField
                    label="Video Path"
                    name="video_path"
                    value={videoData.video_path}
                    onChange={handleInputChange}
                    fullWidth
                    sx={{
                        mt: 1,
                        '& input': { color: 'white' },
                        '& .MuiOutlinedInput-notchedOutline': { borderColor: 'white' }, // Color del borde
                        '&:hover .MuiOutlinedInput-notchedOutline': { borderColor: 'white' }, // Color del borde al pasar el ratón
                        '& .Mui-focused .MuiOutlinedInput-notchedOutline': { borderColor: '#ED4343' }, // Color del borde cuando está enfocado
                        '& .MuiInputLabel-root': { color: 'white' },
                        mb: 2
                    }}
                />
                <TextField
                    label="Title"
                    name="title"
                    value={videoData.title}
                    onChange={handleInputChange}
                    fullWidth
                    sx={{
                        mt: 1,
                        '& input': { color: 'white' },
                        '& .MuiOutlinedInput-notchedOutline': { borderColor: 'white' }, // Color del borde
                        '&:hover .MuiOutlinedInput-notchedOutline': { borderColor: 'white' }, // Color del borde al pasar el ratón
                        '& .Mui-focused .MuiOutlinedInput-notchedOutline': { borderColor: '#ED4343' }, // Color del borde cuando está enfocado
                        '& .MuiInputLabel-root': { color: 'white' },
                        mb: 2
                    }}
                />
                <TextField
                    label="Views"
                    name="views"
                    type="number"
                    value={videoData.views}
                    onChange={handleInputChange}
                    fullWidth
                    sx={{
                        mt: 1,
                        '& input': { color: 'white' },
                        '& .MuiOutlinedInput-notchedOutline': { borderColor: 'white' }, // Color del borde
                        '&:hover .MuiOutlinedInput-notchedOutline': { borderColor: 'white' }, // Color del borde al pasar el ratón
                        '& .Mui-focused .MuiOutlinedInput-notchedOutline': { borderColor: '#ED4343' }, // Color del borde cuando está enfocado
                        '& .MuiInputLabel-root': { color: 'white' },
                        mb: 2
                    }}
                />
                <TextField
                    label="Duration"
                    name="duration"
                    value={videoData.duration}
                    onChange={handleInputChange}
                    fullWidth
                    sx={{
                        mt: 1,
                        '& input': { color: 'white' },
                        '& .MuiOutlinedInput-notchedOutline': { borderColor: 'white' }, // Color del borde
                        '&:hover .MuiOutlinedInput-notchedOutline': { borderColor: 'white' }, // Color del borde al pasar el ratón
                        '& .Mui-focused .MuiOutlinedInput-notchedOutline': { borderColor: '#ED4343' }, // Color del borde cuando está enfocado
                        '& .MuiInputLabel-root': { color: 'white' },
                        mb: 2
                    }}
                />
                <TextField
                    label="Likes"
                    name="likes"
                    type="number"
                    value={videoData.likes}
                    onChange={handleInputChange}
                    fullWidth
                    sx={{
                        mt: 1,
                        '& input': { color: 'white' },
                        '& .MuiOutlinedInput-notchedOutline': { borderColor: 'white' }, // Color del borde
                        '&:hover .MuiOutlinedInput-notchedOutline': { borderColor: 'white' }, // Color del borde al pasar el ratón
                        '& .Mui-focused .MuiOutlinedInput-notchedOutline': { borderColor: '#ED4343' }, // Color del borde cuando está enfocado
                        '& .MuiInputLabel-root': { color: 'white' },
                        mb: 2
                    }}
                />
                <TextField
                    label="Description"
                    name="description"
                    multiline
                    rows={4}
                    value={videoData.description}
                    onChange={handleInputChange}
                    fullWidth
                    sx={{
                        mt: 1,
                        '& input, & textarea': { color: 'white' }, // Aplica el color blanco al texto
                        '& .MuiOutlinedInput-notchedOutline': { borderColor: 'white' }, // Color del borde
                        '&:hover .MuiOutlinedInput-notchedOutline': { borderColor: 'white' }, // Color del borde al pasar el ratón
                        '& .Mui-focused .MuiOutlinedInput-notchedOutline': { borderColor: '#ED4343' }, // Color del borde cuando está enfocado
                        '& .MuiInputLabel-root': { color: 'white' },
                        mb: 2
                    }}
                />
                <TextField
                    label="Author"
                    name="author"
                    value={videoData.author}
                    onChange={handleInputChange}
                    fullWidth
                    sx={{
                        mt: 1,
                        '& input': { color: 'white' },
                        '& .MuiOutlinedInput-notchedOutline': { borderColor: 'white' }, // Color del borde
                        '&:hover .MuiOutlinedInput-notchedOutline': { borderColor: 'white' }, // Color del borde al pasar el ratón
                        '& .Mui-focused .MuiOutlinedInput-notchedOutline': { borderColor: '#ED4343' }, // Color del borde cuando está enfocado
                        '& .MuiInputLabel-root': { color: 'white' },
                        mb: 2
                    }}
                />
                <Button variant="contained" color='error' onClick={handleSubmit} sx={{ mt: 2 }}>
                    Agregar Video
                </Button>

                {/* Snackbar para mostrar mensaje de error */}
                <Snackbar
                    open={errorOpen}
                    autoHideDuration={6000}
                    onClose={handleCloseError}
                    message="Por favor, completa todos los campos"
                    anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
                />

                {/* Snackbar para mostrar mensaje de éxito */}
                <Snackbar
                    open={successOpen}
                    autoHideDuration={6000}
                    onClose={handleCloseSuccess}
                    message="Video agregado exitosamente"
                    anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
                />
            </Box>
        </Modal>
    );
};

export default VideoForModal;
