import React from 'react'
import { useState, useEffect } from 'react';
import { Link, useParams } from 'react-router-dom';
import ReactPlayer from 'react-player';
import { Typography, Box, Stack } from '@mui/material';
import { CheckCircle } from '@mui/icons-material/CheckCircle';
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import videotest from "../assets/videos/VIDEO-TEST.mp4";
import { fetchData } from '../utils/api.js';

import { Video, Videos } from './';
import { demoVideoUrl, demoVideoTitle, demoChannelTitle, demoChannelUrl } from '../utils/constants';

const VideoDetail = () => {

  const { id: videoId } = useParams();
  const [videos, setVideos] = useState([]);
  const [videoUrl, setVideoUrl] = useState('');
  const [videoInfo, setVideoInfo] = useState({});
  const [selectedCategory, setSelectedCategory] = useState('Nuevo');
  const [videoCount, setVideoCount] = useState(0);
  console.log('ID video:', videoId);

  useEffect(() => {
    // Intentar obtener la ruta del video desde el puerto 5000
    fetchVideoPath(`http://localhost:5000/get_video_path/${videoId}`);
  }, [videoId]);

  useEffect(() => {
    // Intentar obtener la información del video desde el puerto 5000
    fetchVideoInfo(`http://localhost:5000/get_video_info/${videoId}`);
  }, [videoId]);

  useEffect(() => {
    // Intentar obtener la categoría del video desde el puerto 5000
    fetchVideoCategory(`http://localhost:5000/get_video_category/${videoId}`);
  }, [videoId]);

  useEffect(() => {
    // Utiliza la función fetchData desde el archivo api.js para obtener videos relacionados
    fetchData(selectedCategory, setVideoCount, setVideos);
    console.log('El parametro Videos Contiene', videos);
  }, [selectedCategory]);

  const fetchVideoPath = async (url) => {
    try {
      const response = await fetch(url);
      const data = await response.json();
      setVideoUrl(data.video_path);
      console.log('Direccion del video:', data.video_path);
    } catch (error) {
      // Si hay un error al acceder al puerto 5000, intentar con el puerto 8000
      fetchVideoPath(`http://localhost:8000/get_video_path/${videoId}`);
    }
  };

  const fetchVideoInfo = async (url) => {
    try {
      const response = await fetch(url);
      const data = await response.json();
      setVideoInfo(data);
      console.log('Data del video:', data);
    } catch (error) {
      // Si hay un error al acceder al puerto 5000, intentar con el puerto 8000
      fetchVideoInfo(`http://localhost:8000/get_video_info/${videoId}`);
    }
  };

  const fetchVideoCategory = async (url) => {
    try {
      const response = await fetch(url);
      const data = await response.json();
      console.log('Categoría del video:', data.category);
      setSelectedCategory(data.category);
    } catch (error) {
      // Si hay un error al acceder al puerto 5000, intentar con el puerto 8000
      fetchVideoCategory(`http://localhost:8000/get_video_category/${videoId}`);
    }
  };

  return (
    <Box minHeight="95vh">
      <Stack direction={{ xs: 'column', md: 'row' }}>
        <Box flex={1}>
          <Box sx={{ width: '100%', position: 'sticky', top: '86px' }}>
            <ReactPlayer url={videotest} className="react-player" controls />
            <Typography color="#fff" variant='h5' fontWeight="bold" p={2}>
              {demoVideoTitle}
            </Typography>
            <Stack direction="row" justifyContent="space-between" sx={{ color: "#fff" }} py={1} px={2}>
              <Link to={demoChannelUrl}>
                <Typography variant={{ sm: "subtitle1", md: 'h6' }} color="#fff" >
                  Autor
                  <CheckCircleIcon sx={{ fontSize: "12px", color: "gray", ml: "5px" }} />
                </Typography>
              </Link>
              <Stack direction="row" gap="20px" alignItems="center">
                <Typography variant="body1" sx={{ opacity: 0.7 }}>
                  {parseInt(100).toLocaleString()} views
                </Typography>
                <Typography variant="body1" sx={{ opacity: 0.7 }}>
                  {parseInt(100).toLocaleString()} likes
                </Typography>
              </Stack>
            </Stack>
          </Box>
        </Box>
        <Box px={2} py={{ md: 1, xs: 5 }} justifyContent="center" alignItems="center" >
          <Videos videos={videos} direction="column" />
        </Box>
      </Stack>
    </Box>
  )
}

export default VideoDetail