import React from 'react'
import { useState, useEffect } from 'react';
import { Box, Typography } from '@mui/material';
import { Videos } from './';
import { useParams } from 'react-router-dom';

import { fetchFromAPI } from '../utils/fetchFromAPI';

const SearchFeed = () => {

  const [videos, setVideos] = useState([]);
  const { searchTerm } = useParams();

  useEffect(() => {
    // Intentar realizar la solicitud al puerto 5000
    fetchSearchResults(`http://localhost:5000/search_videos/${searchTerm}`);
  }, [searchTerm]);

  const fetchSearchResults = async (url) => {
    try {
      const response = await fetch(url);
      const data = await response.json();
      setVideos(data);
    } catch (error) {
      // Si hay un error al acceder al puerto 5000, intentar con el puerto 8000
      fetchSearchResults(`http://localhost:8000/search_videos/${searchTerm}`);
    }
  };

  return (
    <Box p={2} sx={{ overflowY: 'auto', height: '90vh', flex: 2 }}>
      <Typography variant='h4' fontWeight="bold" mb={2} sx={{ color: 'white' }}>
        Search Results For: <span style={{ color: '#F31503' }}>{searchTerm}</span> videos
      </Typography>

      <Videos videos={videos} />

    </Box>
  )
}

export default SearchFeed