import React, { useState, useEffect } from 'react';
import { Stack, Button, Menu, MenuItem, Hidden } from '@mui/material';
import { Link } from 'react-router-dom';
import { logo } from '../utils/constants';
import SearchBar from './SearchBar';
import VideoForModal from './VideoForModal';
import VideoFormModal from './VideoFormModal';
import MenuIcon from '@mui/icons-material/Menu';

const Navbar = () => {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isModalOpen2, setIsModalOpen2] = useState(false);
  const [anchorEl, setAnchorEl] = useState(null);
  const [videos, setVideos] = useState([]);
  const [categories, setCategories] = useState([]);

  useEffect(() => {
    // Intentar realizar la solicitud al puerto 5000
    fetchVideosAndCategories('http://localhost:5000/get_video_categories');
  }, []);

  const fetchVideosAndCategories = async (url) => {
    try {
      const response = await fetch(url);
      const data = await response.json();
      console.log("Server Response:", data); // Para depuración

      // Si los datos están en el formato esperado
      if (Array.isArray(data.videos) && Array.isArray(data.categories)) {
        // Convierte cada elemento en los arrays a un objeto
        const formattedVideos = data.videos.map(videoArray => {
          return {
            id: videoArray[0],
            thumbnail_path: videoArray[1],
            title: videoArray[2],
            views: videoArray[3],
            duration: videoArray[4],
            likes: videoArray[5],
            description: videoArray[6],
            video_path: videoArray[7],
            author: videoArray[8]
          };
        });

        const formattedCategories = data.categories.map(categoryArray => {
          return {
            id: categoryArray[0],
            name: categoryArray[1]
          };
        });

        // Actualiza los estados con los datos formateados
        setVideos(formattedVideos);
        setCategories(formattedCategories);
      } else {
        // Si los datos no están en el formato esperado, intentar con el puerto 8000
        fetchVideosAndCategories('http://localhost:8000/get_video_categories');
      }
    } catch (error) {
      // Si hay un error al acceder al puerto 5000, intentar con el puerto 8000
      fetchVideosAndCategories('http://localhost:8000/get_video_categories');
    }
  };

  const handleOpenModal = () => {
    setIsModalOpen(true);
    handleCloseMenu();
  };

  const handleCloseModal = () => {
    setIsModalOpen(false);
  };

  const handleOpenModal2 = () => {
    setIsModalOpen2(true);
    handleCloseMenu();
  };

  const handleCloseModal2 = () => {
    setIsModalOpen2(false);
  };

  const handleOpenMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleCloseMenu = () => {
    setAnchorEl(null);
  };

  return (
    <Stack
      direction="row"
      alignItems="center"
      p={2}
      sx={{
        position: 'sticky',
        background: '#000',
        top: 0,
        justifyContent: 'space-between',
      }}
    >

      <Menu
        anchorEl={anchorEl}
        open={Boolean(anchorEl)}
        onClose={handleCloseMenu}
      >
        <MenuItem onClick={handleOpenModal}>Agregar Video</MenuItem>
        <MenuItem onClick={handleOpenModal2}>Asignar Categorias</MenuItem>
      </Menu>

      <Link to="/" style={{ display: 'flex', alignItems: 'center' }}>
        <img src={logo} alt="logo" height={45} />
      </Link>
      <SearchBar />
      <Button onClick={handleOpenMenu} sx={{ color: 'white' }}>
        <MenuIcon />
      </Button>

      {Boolean(anchorEl) ? null : (
        <Hidden xlUp>
          {/* Se oculta en dispositivos móviles */}
          <Button
            variant="contained"
            onClick={handleOpenModal}
            mt={2}
            sx={{
              backgroundColor: '#ED4343',
              '&:hover': { backgroundColor: '#ED4343' },
              display: { xs: 'none', sm: 'none', md: 'block' }
            }}
          >
            Agregar Video
          </Button>
          <Button
            variant="contained"
            onClick={handleOpenModal2}
            mt={2}
            sx={{
              backgroundColor: '#FBC101',
              '&:hover': { backgroundColor: '#FBC101' },
              display: { xs: 'none', sm: 'none', md: 'block' }
            }}
          >
            Asignar Categorias
          </Button>
        </Hidden>
      )}

      <VideoForModal isOpen={isModalOpen} handleClose={handleCloseModal} />
      <VideoFormModal isOpen={isModalOpen2} handleClose={handleCloseModal2} videos={videos} categories={categories} />
    </Stack>
  );
};

export default Navbar;
